import Bmob from "hydrogen-js-sdk";
let that;
export default {
	data() {
		return {
			detail: ''
		}
	},
	onLoad(options) {
		that = this;
		const query = Bmob.Query("bad_goods");
		query.equalTo("goods", "==", options.id);
		query.order("-createdAt");
		query.find().then(res => {
			console.log(res)
			that.detail = res
		});
	},
	methods: {

	}
}
